// src/features/drone-early-inform/DroneEarlyInformSettings.jsx
"use client"

import * as React from "react"
import {
  CheckCircle2,
  Pencil,
  PlusCircle,
  RefreshCw,
  Save,
  Search,
  Trash2,
  XCircle,
} from "lucide-react"
import { toast } from "sonner"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { buildToastOptions } from "@/features/line-dashboard/components/data-table/utils/toast"

const SUCCESS_STYLE = buildToastOptions({ color: "#065f46" })
const ERROR_STYLE = buildToastOptions({ color: "#991b1b", duration: 3000 })

const EMPTY_FORM = {
  line_id: "",
  main_step: "",
  custom_end_step: "",
}

function toNullable(value) {
  const trimmed = typeof value === "string" ? value.trim() : ""
  return trimmed.length > 0 ? trimmed : null
}

export function DroneEarlyInformSettings({ lineId }) {
  const normalizedLineId = typeof lineId === "string" ? lineId : ""

  const [queryLineId, setQueryLineId] = React.useState(normalizedLineId)
  const [activeLineId, setActiveLineId] = React.useState(normalizedLineId)
  const [rows, setRows] = React.useState([])
  const [isLoading, setIsLoading] = React.useState(false)
  const [error, setError] = React.useState(null)
  const [refreshKey, setRefreshKey] = React.useState(0)

  const [formValues, setFormValues] = React.useState({
    ...EMPTY_FORM,
    line_id: normalizedLineId,
  })
  const [creating, setCreating] = React.useState(false)

  const [editingId, setEditingId] = React.useState(null)
  const [editValues, setEditValues] = React.useState(EMPTY_FORM)
  const [savingRowId, setSavingRowId] = React.useState(null)
  const [deletingRowId, setDeletingRowId] = React.useState(null)

  React.useEffect(() => {
    setQueryLineId(normalizedLineId)
    setActiveLineId(normalizedLineId)
    setFormValues((prev) => ({ ...prev, line_id: normalizedLineId }))
  }, [normalizedLineId])

  React.useEffect(() => {
    const controller = new AbortController()
    let cancelled = false

    async function loadRows() {
      setIsLoading(true)
      setError(null)

      try {
        const params = new URLSearchParams()
        if (activeLineId) {
          params.set("lineId", activeLineId)
        }

        const query = params.toString()
        const response = await fetch(
          `/api/drone-early-inform${query ? `?${query}` : ""}`,
          {
            method: "GET",
            signal: controller.signal,
          }
        )

        if (!response.ok) {
          const payload = await response.json().catch(() => ({}))
          const message = typeof payload?.error === "string" ? payload.error : "데이터를 불러오지 못했습니다."
          throw new Error(message)
        }

        const payload = await response.json()
        if (!cancelled) {
          setRows(Array.isArray(payload?.rows) ? payload.rows : [])
        }
      } catch (loadError) {
        if (controller.signal.aborted) {
          return
        }
        const message =
          loadError instanceof Error ? loadError.message : "데이터를 불러오지 못했습니다."
        if (!cancelled) {
          setError(message)
          setRows([])
        }
      } finally {
        if (!cancelled) {
          setIsLoading(false)
        }
      }
    }

    void loadRows()

    return () => {
      cancelled = true
      controller.abort()
    }
  }, [activeLineId, refreshKey])

  const handleApplyFilter = React.useCallback(() => {
    const nextValue = queryLineId.trim()
    setError(null)
    setActiveLineId((current) => {
      if (current === nextValue) {
        setRefreshKey((key) => key + 1)
        return current
      }
      return nextValue
    })
  }, [queryLineId])

  const handleResetFilter = React.useCallback(() => {
    setQueryLineId("")
    setError(null)
    setActiveLineId((current) => {
      if (current === "") {
        setRefreshKey((key) => key + 1)
        return current
      }
      return ""
    })
  }, [])

  const handleChangeForm = (field) => (event) => {
    const value = event?.target?.value ?? ""
    setError(null)
    setFormValues((prev) => ({ ...prev, [field]: value }))
  }

  const handleCreateRow = async (event) => {
    event?.preventDefault()

    const nextValues = {
      line_id: formValues.line_id.trim(),
      main_step: formValues.main_step.trim(),
      custom_end_step: formValues.custom_end_step.trim(),
    }

    if (!nextValues.line_id || !nextValues.main_step) {
      setError("line_id와 main_step은 필수입니다.")
      return
    }

    setCreating(true)
    setError(null)

    try {
      const response = await fetch("/api/drone-early-inform", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          line_id: nextValues.line_id,
          main_step: nextValues.main_step,
          custom_end_step: toNullable(nextValues.custom_end_step),
        }),
      })

      if (!response.ok) {
        const payload = await response.json().catch(() => ({}))
        const message = typeof payload?.error === "string" ? payload.error : "행을 추가하지 못했습니다."
        throw new Error(message)
      }

      await response.json()

      toast.success("행이 추가되었습니다.", {
        description: `${nextValues.line_id} / ${nextValues.main_step}`,
        icon: <CheckCircle2 className="h-5 w-5 text-emerald-500" />,
        ...SUCCESS_STYLE,
      })

      setFormValues((prev) => ({ ...prev, main_step: "", custom_end_step: "" }))
      if (!activeLineId || activeLineId === nextValues.line_id) {
        setRefreshKey((key) => key + 1)
      }
    } catch (createError) {
      const message = createError instanceof Error ? createError.message : "행을 추가하지 못했습니다."
      setError(message)
      toast.error("추가 실패", {
        description: message,
        icon: <XCircle className="h-5 w-5 text-red-500" />,
        ...ERROR_STYLE,
      })
    } finally {
      setCreating(false)
    }
  }

  const startEditing = (row) => {
    setEditingId(row.id)
    setEditValues({
      line_id: row.line_id ?? "",
      main_step: row.main_step ?? "",
      custom_end_step: row.custom_end_step ?? "",
    })
  }

  const cancelEditing = () => {
    setEditingId(null)
    setEditValues(EMPTY_FORM)
  }

  const handleEditChange = (field) => (event) => {
    const value = event?.target?.value ?? ""
    setError(null)
    setEditValues((prev) => ({ ...prev, [field]: value }))
  }

  const handleSaveRow = async (row) => {
    if (!editingId) return

    const payload = {
      line_id: editValues.line_id.trim(),
      main_step: editValues.main_step.trim(),
      custom_end_step: toNullable(editValues.custom_end_step),
    }

    if (!payload.line_id || !payload.main_step) {
      setError("line_id와 main_step은 필수입니다.")
      return
    }

    const baseLineId = typeof row.line_id === "string" ? row.line_id.trim() : ""
    const baseMainStep = typeof row.main_step === "string" ? row.main_step.trim() : ""
    const baseCustom = row.custom_end_step ?? ""

    const changed =
      payload.line_id !== baseLineId ||
      payload.main_step !== baseMainStep ||
      (payload.custom_end_step ?? "") !== baseCustom

    if (!changed) {
      cancelEditing()
      return
    }

    setSavingRowId(row.id)
    setError(null)

    try {
      const response = await fetch(`/api/drone-early-inform/${row.id}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      })

      if (!response.ok) {
        const body = await response.json().catch(() => ({}))
        const message = typeof body?.error === "string" ? body.error : "행을 수정하지 못했습니다."
        throw new Error(message)
      }

      await response.json()

      toast.success("행이 업데이트되었습니다.", {
        description: `${payload.line_id} / ${payload.main_step}`,
        icon: <CheckCircle2 className="h-5 w-5 text-emerald-500" />,
        ...SUCCESS_STYLE,
      })

      cancelEditing()
      setRefreshKey((key) => key + 1)
    } catch (updateError) {
      const message = updateError instanceof Error ? updateError.message : "행을 수정하지 못했습니다."
      setError(message)
      toast.error("수정 실패", {
        description: message,
        icon: <XCircle className="h-5 w-5 text-red-500" />,
        ...ERROR_STYLE,
      })
    } finally {
      setSavingRowId(null)
    }
  }

  const handleDeleteRow = async (row) => {
    const confirmed = window.confirm(`ID ${row.id} 행을 삭제하시겠습니까?`)
    if (!confirmed) return

    setDeletingRowId(row.id)
    setError(null)

    try {
      const response = await fetch(`/api/drone-early-inform/${row.id}`, {
        method: "DELETE",
      })

      if (!response.ok) {
        const payload = await response.json().catch(() => ({}))
        const message = typeof payload?.error === "string" ? payload.error : "행을 삭제하지 못했습니다."
        throw new Error(message)
      }

      toast.success("행이 삭제되었습니다.", {
        description: `${row.line_id ?? "-"} / ${row.main_step ?? "-"}`,
        icon: <CheckCircle2 className="h-5 w-5 text-emerald-500" />,
        ...SUCCESS_STYLE,
      })

      if (editingId === row.id) {
        cancelEditing()
      }

      setRefreshKey((key) => key + 1)
    } catch (deleteError) {
      const message = deleteError instanceof Error ? deleteError.message : "행을 삭제하지 못했습니다."
      setError(message)
      toast.error("삭제 실패", {
        description: message,
        icon: <XCircle className="h-5 w-5 text-red-500" />,
        ...ERROR_STYLE,
      })
    } finally {
      setDeletingRowId(null)
    }
  }

  return (
    <div className="flex h-full flex-col gap-4 overflow-y-auto p-2">
      <section className="rounded-lg border bg-card p-4 shadow-sm">
        <div className="flex flex-col gap-3 sm:flex-row sm:items-end sm:justify-between">
          <div className="flex flex-1 flex-col gap-2 sm:max-w-md">
            <label className="text-sm font-medium text-muted-foreground">Line ID 필터</label>
            <div className="flex gap-2">
              <Input
                value={queryLineId}
                onChange={(event) => setQueryLineId(event.target.value)}
                placeholder="라인 ID를 입력하세요"
              />
              <Button
                type="button"
                variant="outline"
                onClick={handleApplyFilter}
                disabled={isLoading}
              >
                <Search className="h-4 w-4" />
                적용
              </Button>
              <Button type="button" variant="ghost" onClick={handleResetFilter} disabled={isLoading}>
                초기화
              </Button>
            </div>
          </div>
          <Button
            type="button"
            variant="outline"
            onClick={() => setRefreshKey((key) => key + 1)}
            disabled={isLoading}
            className="sm:self-start"
          >
            <RefreshCw className="h-4 w-4" />
            새로고침
          </Button>
        </div>
      </section>

      <section className="rounded-lg border bg-card p-4 shadow-sm">
        <form className="grid gap-3 md:grid-cols-4" onSubmit={handleCreateRow}>
          <div className="md:col-span-1">
            <label className="mb-1 block text-sm font-medium">Line ID</label>
            <Input value={formValues.line_id} onChange={handleChangeForm("line_id")} required />
          </div>
          <div className="md:col-span-1">
            <label className="mb-1 block text-sm font-medium">Main Step</label>
            <Input value={formValues.main_step} onChange={handleChangeForm("main_step")} required />
          </div>
          <div className="md:col-span-1">
            <label className="mb-1 block text-sm font-medium">Custom End Step</label>
            <Input value={formValues.custom_end_step} onChange={handleChangeForm("custom_end_step")} />
          </div>
          <div className="md:col-span-1 flex items-end">
            <Button type="submit" disabled={creating} className="w-full">
              <PlusCircle className="h-4 w-4" />
              추가
            </Button>
          </div>
        </form>
      </section>

      {error ? (
        <div className="rounded-md border border-destructive/30 bg-destructive/10 px-3 py-2 text-sm text-destructive">
          {error}
        </div>
      ) : null}

      <section className="flex-1 overflow-hidden rounded-lg border bg-card shadow-sm">
        <TableContainer className="max-h-[60vh]">
          <Table stickyHeader>
            <TableHeader>
              <TableRow>
                <TableHead className="w-[80px]">ID</TableHead>
                <TableHead>Line ID</TableHead>
                <TableHead>Main Step</TableHead>
                <TableHead>Custom End Step</TableHead>
                <TableHead className="w-[160px] text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {isLoading ? (
                <TableRow>
                  <TableCell colSpan={5} className="text-center text-sm text-muted-foreground">
                    데이터를 불러오는 중입니다...
                  </TableCell>
                </TableRow>
              ) : rows.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={5} className="text-center text-sm text-muted-foreground">
                    표시할 데이터가 없습니다.
                  </TableCell>
                </TableRow>
              ) : (
                rows.map((row) => {
                  const isEditing = editingId === row.id
                  const isSaving = savingRowId === row.id
                  const isDeleting = deletingRowId === row.id

                  return (
                    <TableRow key={row.id} data-state={isEditing ? "selected" : undefined}>
                      <TableCell>{row.id}</TableCell>
                      <TableCell>
                        {isEditing ? (
                          <Input value={editValues.line_id} onChange={handleEditChange("line_id")} />
                        ) : (
                          row.line_id
                        )}
                      </TableCell>
                      <TableCell>
                        {isEditing ? (
                          <Input value={editValues.main_step} onChange={handleEditChange("main_step")} />
                        ) : (
                          row.main_step
                        )}
                      </TableCell>
                      <TableCell>
                        {isEditing ? (
                          <Input
                            value={editValues.custom_end_step}
                            onChange={handleEditChange("custom_end_step")}
                          />
                        ) : (
                          row.custom_end_step ?? ""
                        )}
                      </TableCell>
                      <TableCell>
                        <div className="flex justify-end gap-2">
                          {isEditing ? (
                            <>
                              <Button
                                type="button"
                                size="sm"
                                onClick={() => handleSaveRow(row)}
                                disabled={isSaving}
                              >
                                <Save className="h-4 w-4" />
                                저장
                              </Button>
                              <Button type="button" size="sm" variant="ghost" onClick={cancelEditing} disabled={isSaving}>
                                취소
                              </Button>
                            </>
                          ) : (
                            <>
                              <Button
                                type="button"
                                size="sm"
                                variant="outline"
                                onClick={() => startEditing(row)}
                                disabled={isDeleting}
                              >
                                <Pencil className="h-4 w-4" />
                                수정
                              </Button>
                              <Button
                                type="button"
                                size="sm"
                                variant="destructive"
                                onClick={() => handleDeleteRow(row)}
                                disabled={isDeleting}
                              >
                                <Trash2 className="h-4 w-4" />
                                삭제
                              </Button>
                            </>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  )
                })
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </section>
    </div>
  )
}
